<template>
    <div ref="chart" class="task-chart" :style="{ width: '100%', height: '400px' }"></div>
  </template>
  
  <script>
  import * as echarts from 'echarts';
  
  export default {
    name: 'TaskChart',
    props: {
      tasks: {
        type: Array,
        required: true,
      },
    },
    data() {
      return {
        chartInstance: null, // 保存图表实例
      };
    },
    mounted() {
      this.initChart();
      window.addEventListener('resize', this.resizeChart);
    },
    beforeUnmount() {
      // 组件卸载前移除事件监听和销毁图表实例
      window.removeEventListener('resize', this.resizeChart);
      if (this.chartInstance) {
        this.chartInstance.dispose();
      }
    },
    methods: {
      initChart() {
        this.chartInstance = echarts.init(this.$refs.chart);
        const option = this.getChartOptions();
        this.chartInstance.setOption(option);
      },
      getChartOptions() {
  
  // 获取任务名称作为横坐标
  const taskNames = this.tasks.map(task => task.name);

  const priorities = this.tasks.map(task => task.priority); // 获取优先级
  const completionRates = this.tasks.map(task => task.completionRate); // 获取完成度

  return {
    title: {
      text: '待办事项概览',
    },
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'cross',
        label: {
          backgroundColor: '#6a7985',
        },
      },
    },
    legend: {
      data: ['优先级', '完成度'],
    },
    xAxis: {
      type: 'category',
      data: taskNames, // 使用任务名称作为横坐标
      axisLabel: {
        interval: 0,
        rotate: 45,
      },
    },
    yAxis: [
      {
        type: 'value',
        name: '优先级',
        min: 0,
        max: 5, // 假设优先级是1到5的整数
        interval: 1,
        axisLabel: {
          formatter: '{value}',
        },
      },
      {
        type: 'value',
        name: '完成度',
        min: 0,
        max: 100, // 完成度是百分比
        interval: 20,
        axisLabel: {
          formatter: '{value} %',
        },
      },
    ],
    series: [
      {
        name: '优先级',
        type: 'bar',
        data: priorities,
        yAxisIndex: 0,
      },
      {
        name: '完成度',
        type: 'line',
        data: completionRates,
        yAxisIndex: 1,
      },
    ],
  };
  },

      resizeChart() {
        if (this.chartInstance) {
          this.chartInstance.resize();
        }
      },
    },
  };
  </script>
  
  <style scoped>
  .task-chart {
    border: 1px solid #dcdfe6; /* 更柔和的边框颜色 */
  }
  </style>